@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }

}

@mixin fade-in {
  animation: fade-in 0.2s ease-in-out;
}

@mixin fade-out {
  animation: fade-out 0.2s ease-in-out;
}
